.slider-item {
    width: 100%;
    height: 25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 1em;
    padding-left: 4em;
    color: white;
    background-position: center;
    background-size: cover;
}

.slider-item h1 {
    font-size: 4em;

}

.slider-item a {
    font-size: 1.4em;
    text-decoration: none;
    padding-bottom: 0.5em;
    border-bottom: 1px solid #FAFAFAAC;
}

@media screen and (max-width:1280px) {
    .slider-item {
        font-size: 0.5em;

    }

    .slider-item a {
        font-size: 1.8em;

    }
}